.Lista{
    height: 100%;
    width: 30%;
    border-radius: 10px;
    padding: 10px;
    margin-top: 10px;
    position: relative;
    left: 1%;
    background-color: black;
   
    
}

.Lista li 
{
    display: inline;
    padding-left: 5%;
    padding-right: 5%;
    position: relative;
    left: 0%;
    
}
button{
    border: 200px;
    position: relative;
    color:#FF6464;

    background-color: black;
    border: none;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}
button:hover {
    
    color:#FF3232;
}
@media only screen and (min-width: 1024px) {
    .Lista{
        
        width: 30%;
        left: 68.9%;
    }
  }
  @media only screen and (max-width: 1023px) {
    .Lista {
      display:flex;
      width: 105%;
    }
  }