.Fperfil{
    border-radius: 50%;
    position: absolute;

  }
  .Ctext{
    text-align: center;
    padding: 40px;
    font-weight: 400;
    line-height: 1.5rem;
    position : relative;
  }
  .text{
        font-size: 1.5rem;
        text-align: justify;
        color: cadetblue;
        position : relative;
    }
  .perfil{
    background-image: url(../imagenes/steel.png); 
    left: center;
    position: relative;
      
  }
  .Titulo
  {
    font-size: 2rem;
    font-weight: 400;
    color: cadetblue;
    text-align: center;
    margin: 0px;
    padding: 0px;
    font-family: Impact, 'Arial Narrow Bold', sans-serif;
  }
  .Titulo2
  {
    color: cadetblue;
    text-align: left;
    
  }
.Listas li
{
  color: cadetblue;
  text-align: left;
  list-style-type: none;
}
.Mli
{
  float: left;
  list-style-type: none;
}
@media only screen and (min-width: 1024px) {
  .perfil {
    display:flex;
    height: 600px;
  }
  .Fperfil
  {
    width: 20%;
    height: 500px;
    border-radius: 50%;
    position: relative;
  }
}
@media only screen and (max-width: 1023px) {
  .perfil {
    display:column;
    width: 118%;
    height: 1400px;
  }
  .Fperfil
  {
    width: 80%;
    top: 30px; 
    height: auto;
    border-radius: 50%;
    position: relative;
  }
}