.banner
{
    position: relative;
    text-align: center;
    width: 100%;
    height:  1080px;
}
.banner-text
{
    position: absolute;
    top: 35%;
    left: 58%;
    transform: translate(-50%, -50%);
    color: cadetblue;
    border: 15ch;
    font-size: 100px;
    font-family: Impact, 'Arial Narrow Bold', sans-serif;
    max-width: 100%;
    max-height: 100%;
}


.Imga
{
    display: block;
    position: absolute;
    overflow: hidden;
    width: 30%;
    top: 10%;
    left: 10%;
}
#subtitulo
{   
    top: 50%;

}


@media only screen and (max-width: 1023px) {
    #subtitulo
    {
        top: 60%;
        font-size: 60px;
    }
}