.contenedor{
    width: 350px;
    height: 230px;
    justify-content: center;
    align-items: center;
    position: relative;
    border-radius: 25px;
    overflow: hidden;
    background-color: white;


}

.Img{
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        overflow: hidden;

    
}
.contenido{
    width: 0%;
    height: 100%;
    align-items: center;
    top: 0;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    transition: all 400ms ease-in-out;
    background-color: white;
    border-radius: 30px;

}
.contenedor:hover .contenido{
   width: 100%;
   overflow: hidden;
}
.contenido h2{
    font-size: 20px;
    font-weight: bold;
    text-align: center;	
    padding: 0.8em;
}
.Leng {
    width: 80px;
    height: 80px;
    
}
.contenedor h2 
{
    font-family: monospace;  
}